function CotactText(props){

	// console.log(props.propsValue);


	let data=props.propsValue;
	let {name, href}=data;
	return( 
        <div className="desc">
            <strong>
                <span className="one">   .</span>
                <span className="two">        ,</span>
                 ,  ' .
            </strong>
            <div className="ga"  data-aos="fade-down" data-aos-duration="300">
                <p>010. 5378. 7664</p>
                <span>sjwehcl669@naver.com</span>
            </div>
            <div className="open"  data-aos="fade-down" data-aos-duration="600">
                <p>Open Platform </p>
                <ul>
				{
                    name.map((d, i) => <li key={i}><a href={href[i]}>{d}</a></li>)
                }
                    {/* 
					<li><a href="">CodePen</a></li>
                    <li><a href="">GitHub</a></li>
                    <li><a href="">Figma</a></li> 
					*/}
                </ul>
            </div>
        </div>
	);
}

export default CotactText;