import AboutList from "./AboutList";
import { useSelector } from "react-redux";

function About(){

	// let {about}=data;
	
	// let data=useSelector(state => state);
	let data=useSelector(state => state.about);
	let {number, email, address, license}=data;

	return(
        <div className="about">
            <div className="desc">
                <ul data-aos="fade-up" data-aos-duration="300">
                    <p>SEO JANG WON</p>
                    <strong>  </strong>
                    <span>1997. 10. 27</span>
                </ul>
                <ul data-aos="fade-up" data-aos-duration="600">
					
                    <AboutList propsValue={number}/>
                    <AboutList propsValue={email}/>
                    <AboutList propsValue={address}/>
                    {/* 
                    <li><span>Phone</span>010. 5378. 7664</li>
                    <li><span>Email</span>sjwehcl669@naver.com</li>
                    <li><span>Address</span>  </li>
					*/}
                </ul>
                <ul data-aos="fade-up" data-aos-duration="900">
                    <li><p>License</p></li>
					{
						license.map((d, i) => <AboutList key={i} propsValue={d}/>)
					}
					{/* {
						data.license.map((d, i) =>  <li><span>{d.left}</span>{d.right}</li>)
					} */}
                    {/* 
                    <li><span></span>()</li>
                    <li><span>MAP  2</span>(KAIT)</li>
                    <li><span>MOS Word 2007 Expert</span>(Microsoft)</li>
                    <li><span></span>()</li>
                    <li><span>2</span>()</li>
                    <li><span>IPC-A-610 CIS </span>(())</li>
                     */}
                </ul>
            </div>
            <div className="picture" data-aos="fade-up" data-aos-duration="300">
                <a href=""></a>
            </div>
        </div>
	);
}

export default About;